.loginsignup{
    width:100%;
    height:80vh;
    background:#fce3fe;
    padding-top:100px;
}
.loginsignup-container{
    width:380px;
    height:80%;
    background: white;
    margin-top:0px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    padding:30px 60px;
}
.loginsignup-container h1{
    margin:0px 0px;
}
.loginsignup-fields{
    display:flex;
    flex-direction: column;
    gap:25px;
    margin-top: 20px;
}
.loginsignup-fields input{
   height: 52px; 
   width:100%;
   padding-left: 20px;
   border:1px solid #c9c9c9;
   outline:none;
   color:#5c5c5c;
   font-size:12px;

}
.loginsignup-container button{
    height:52px;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
    width:100%;
    color:white;
}
.loginsignup-login{
    margin-top: 20px;
    color:#5c5c5c;
    font-size: 12px;
    font-weight: 500;
}
.loginsignup-login span{
    color:#ff4141;
    font-weight: 600;
}
.loginsignup-agree{
    display: flex;
    align-items: center;
    margin-top: 0px;
    gap:10px;
    color:#5c5c5c;
    font-size: 12px;
    font-weight: 500;
}
@media(max-width:1280px){
    .loginsignup{
        padding-top: 50px;
    }
    .loginsignup-container{
        width:auto;
        max-width:500px;
        max-height:500px;
    }
    .loginsignup-container h1{
        margin:10px 0px;
    }
    .loginsignup-fields{
        gap:20px;
        margin-top:20px;
    }
    .loginsignup-container button{
        height:50px;
        font-size: 16px;
    }
    .loginsignup-fields input{
        height:55px; width:93%;
    }
    .loginsignup-login{
        font-size: 16px;
    }
    .loginsignup-agree{
        font-size: 14px;
        gap:10px;
    }
}
@media(max-width:800px){
    
    .loginsignup-container{
      padding:20px 30px;
    }
    .loginsignup-container h1{
      font-size: 24px;
    }
    
    .loginsignup-fields input{
        height:50px; 
    }
    .loginsignup-container button{
        height: 50px;
        font-size: 16px;
    }
    .loginsignup-login{
        font-size: 16px;
    }
    .loginsignup-agree{
        font-size: 14px;
        gap:10px;
    }
}
@media(max-width:500px){
    .loginsignup{
        width:100vw;
        height:70vh;
        padding-top: 50px ;
        
    }
    .loginsignup-container button{
        height: 40px;
        font-size: 12px;
    }
    .loginsignup-container{
        width:auto;
        margin-left:30px;
        margin-right:30px;
    }
    .loginsignup-container h1{
        font-size: 15px;
    }
    .loginsignup-fields{
        gap:20px;
        margin-top:20px;
    }
    .loginsignup-fields input{
        font-size: 10px;
        height:40px; width:93%;
    }
    .loginsignup-login{
        font-size: 12px;
    }
    .loginsignup-agree{
        font-size: 12px;
        gap:10px;
    }
}
