.newsletter{
    width:75%;
    height:50vh;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin:auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg,#E493B3,lightyellow);
}
.newsletter h1{
    color:#454545;
    font-size: 55px;
    font-weight: 600;
}
.newsletter p{
    color:#454545;
    font-size: 20px;
}
.newsletter div{
    display:flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    width:730px;
    height:70px;
    border-radius: 80px;
    border:1px solid #e3e3e3;
}
.newsletter input{
    width:500px;
    border:none;
    margin-left: 30px;
    outline:none;
    color:#616161;
    font-size: 16px;
    
}
.newsletter div button{
    width:210px;
    height:70px;
    border-radius: 80px;
    background: black;
    color:white;
    font-size: 16px;
    cursor:pointer;
}
.newsletter div button:hover{
    background:#454545;
}
@media(max-width:1280px){
    .newsletter{
        padding:0px 80px;
        margin-bottom: 120px;
        gap:20px;
    }
    .newsletter h1{
        font-size: 36px;
    }
    .newsletter p{
        font-size: 16px;
    }
    .newsletter div{
        width:600px;
        height:60px;
    }
    .newsletter div input{
        width:400px;
    }
    .newsletter div button{
        width:160px;
        height:60px;
    }
}
@media(max-width:1024px){
    .newsletter{
        
        margin-bottom: 60px;
       
    }
    .newsletter h1{
        font-size: 30px;
    }
    .newsletter p{
        font-size: 14px;
    }
    .newsletter div{
        width:500px;
        height:50px;
    }
    .newsletter div input{
        width:300px;
        font-size: 14px;
    }
    .newsletter div button{
        width:120px;
        height:50px;
        font-size: 14px;
    }
}
@media(max-width:800px){
    .newsletter{
        margin-bottom: 30px;
        height:35vh;
    }
    .newsletter h1{
        font-size: 18px;
    }
    .newsletter p{
        font-size: 12px;
    }
    .newsletter div{
        width:400px;
        height:40px;
    }
    .newsletter div input{
        width:200px;
        font-size: 13px;
    }
    .newsletter div button{
        width:100px;
        height:40px;
        font-size: 13px;
    }
}
@media(max-width:500px){
    .newsletter{
        margin-bottom: 30px;
        height:25vh;
        width:100%;
        padding:0;
        gap:15px;
    }
    .newsletter h1{
        font-size: 16px;
    }
    .newsletter p{
        font-size: 13px;
        max-width:200;
        text-align: center;
    }
    .newsletter div{
        width:290px;
        
    }
    .newsletter div input{
        width:130px;
    }

}