.offers{
    width:75%;
    height:70vh;
    display:flex;
    margin:auto;
    padding:0px 140px;
    margin-bottom: 100px;
    background: linear-gradient(180deg,#E493B3,lightyellow);
}
.offers-flex{
    flex:1;
    display:flex;
    flex-direction: column;
    justify-content: center;
}
.offers-left h1{
    color:#171717;
    font-size: 60px;
    font-weight: 600;
}
.offers-left p{
    color:#171717;
    font-size:22px;
    font-weight:600;

}
.offers-right{
    flex:1;
    display:flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
}
@media(max-width:1280px){
    .offers{
        padding:0px 80px;
        margin-bottom: 120px;
    }
    .offers-left h1{
        font-size: 40px;
    }
    .offers-left p{
        font-size: 18px;
    }
    .offers-right img{
        width:300px;
    }
}
@media(max-width:1024px){
    .offers{
        margin-top: 0px;
        padding:20px 60px;
        margin-bottom: 80px;
        height:40vh;
    }
    .offers-left h1{
        font-size: 30px;
    }
    .offers-left p{
        font-size: 16px;
    }
    .offers-right img{
        width:250px;
        margin-bottom: 40px;
        margin-right: 40px;
    }
    .offers-right{
        padding-top: 30px;
    }
}
@media(max-width:800px){
    .offers{
        
        margin-bottom: 70px;
      
    }
    .offers-left h1{
        font-size: 22px;
    }
    .offers-left p{
        font-size: 13px;
    }
    .offers-right img{
        width:220px;
        margin-bottom: 30px;
        margin-right: 30px;
    }
    
}
@media(max-width:500px){
    .offers{
        height:25vh;
        margin-bottom: 50px;
      
    }
    .offers-left h1{
        font-size: 16px;
    }
    .offers-left p{
        font-size: 10px;
    }
    .offers-right img{
        width:150px;
        margin-bottom:40px;
        margin-right:20px;
    }
    
}